import { useEffect, useState } from 'react';
import { adminSupabase } from './App';  // App.js  adminSupabase 
import { Button } from 'reactstrap';

export const Home4 = () => {
    const [activeUserCount, setActiveUserCount] = useState(0);  // 
    const [totalUserCount, setTotalUserCount] = useState(0);    // 
    const [loading, setLoading] = useState(true);  // 
    const [allTips, setAllTips] = useState([]);  // 

    useEffect(() => {
        // 
        const fetchUserCounts = async () => {
            try {
                // 
                const { data: usersData, error: usersError } = await adminSupabase.auth.admin.listUsers();

                if (usersError) {
                    console.error(':', usersError.message);
                    setLoading(false);  // 
                    return;
                }

                const users = usersData?.users || [];  // data.users 

                // 
                setTotalUserCount(users.length);

                // 
                const { data: sessionData, error: sessionError } = await adminSupabase.auth.getSession();

                if (sessionError) {
                    console.error(':', sessionError.message);
                    setLoading(false);  // 
                    return;
                }

                if (!sessionData?.session?.user) {
                    console.log('');
                    setActiveUserCount(0);
                    setLoading(false);
                    return;
                }

                // 
                setActiveUserCount(1);  //  1 
                setLoading(false);  // 
            } catch (error) {
                console.error(':', error.message);
                setLoading(false);  // 
            }
        };

        // 
        const fetchAllTips = async () => {
            try {
                const { data, error } = await adminSupabase
                    .from('kik')
                    .select('myname, mytip');  // myname  mytip 

                if (error) {
                    console.error(':', error.message);
                } else {
                    setAllTips(data);  // 
                }
            } catch (error) {
                console.error(':', error.message);
            }
        };

        fetchUserCounts();  // 
        fetchAllTips();  // 
    }, []);  // 

    return (
        <div>
            {loading ? (
                <p>...</p>  // 
            ) : (
                <>
                    <h3>{totalUserCount}</h3>  {/*  */}
                    <h3>{activeUserCount}</h3> {/*  */}
                    <h2></h2>  {/*  */}

                    <ul>
                        {allTips.length > 0 ? (
                            allTips.map((tip, index) => {
                                // myname
                                if (!tip.myname) return null;  // myname

                                return (
                                    <li key={index}>
                                        <strong>{tip.myname}</strong>:
                                        {/* mytip */}
                                        {tip.mytip ? tip.mytip : ''}
                                    </li>
                                );
                            })
                        ) : (
                            <p></p>
                        )}
                    </ul>
                </>
            )}
        </div>
    );
};
